import React, { useState } from "react";
import { databases, DATABASE_ID, COLLECTION_ID } from "./config";
import { useEffect } from "react";
import { Box, Card, Container, Stack } from "@mui/material";
import PersianSupportTextField from "Global/Components/UnEditableComponents/PersianSupportTextField/PersianSupportTextField";
import { Send } from "@mui/icons-material";
import { ID } from "appwrite";
const ChatApp = () => {
  const [msg, setMsg] = useState();
  const [inputMsg, setInputMsg] = useState();
  const [clearText, setclearText] = useState();
  useEffect(() => {
    GetMessages();
  }, []);
  const GetMessages = async () => {
    const response = await databases.listDocuments(DATABASE_ID, COLLECTION_ID);

    setMsg(response.documents);
  };
  const HandleSendMessage = async () => {
    let payload = {
      body: inputMsg,
    };
    let response = await databases.createDocument(
      DATABASE_ID,
      COLLECTION_ID,
      ID.unique(),
      payload
    );
    console.log("created ! =>", response);
    console.log("message ! =>", msg);
    setInputMsg("");
    setclearText(true);
    setclearText(false);
  };

  return (
    <React.Fragment>
      <Container>
        <Box sx={{ mt: "1em" }}>
          <PersianSupportTextField
            label="type here..."
            icon={<Send />}
            iconFn={HandleSendMessage}
            isEnglish={true}
            onChange={(val) => setInputMsg(val)}
            clearText={clearText}
          />
        </Box>
        <Stack sx={{ mt: "1em", p: "1em" }} gap="1em">
          {msg ? (
            msg?.map((message) => (
              <Card sx={{ p: ".5em" }} key={message?.$id}>
                {message?.body}
              </Card>
            ))
          ) : (
            <></>
          )}
        </Stack>
      </Container>
    </React.Fragment>
  );
};

export default ChatApp;
